@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
.social-button:active svg {
  opacity: 0.3;
}
.page-footer__copyright__link {
  color: #000000;
}
.page-footer__copyright__link:hover {
  color: #81b3d2;
}
.page-footer__copyright__link:active svg {
  opacity: 0.3;
}
@media (min-width: 1200px) {
  .button-like {
    color: #ffffff;
  }
}
@media (min-width: 1200px) {
  .button-like svg {
    opacity: 1;
  }
}
.button-playback:hover svg {
  fill: #333333;
}
.button-playback:active svg {
  fill: #333333;
  opacity: 0.3;
}
@media (min-width: 768px) {
  .button-playback svg {
    fill: #ffffff;
  }
}
@media (min-width: 768px) {
  .button-playback:hover svg {
    fill: #ffffff;
    opacity: 0.6;
  }
}
@media (min-width: 768px) {
  .button-playback:active svg {
    fill: #ffffff;
    opacity: 0.3;
  }
}
.advantages-item__background-blue {
  background-color: #81b3d2;
  color: #ffffff;
}
.advantages-item__background-blue__pic-town,
.advantages-item__background-blue__pic-canyon {
  display: grid;
  grid-template-rows: min-content min-content;
}
@media (min-width: 768px) {
  .advantages-item__background-blue__pic-town,
  .advantages-item__background-blue__pic-canyon {
    grid-template-columns: repeat(3, 1fr);
    padding: 0;
  }
}
.advantages-item__background-blue__pic-town {
  background-image: url("../img/mobile/advantages-triangle.svg");
  background-repeat: no-repeat;
  background-position: center -16px;
  background-size: 768px 37px;
}
@media (min-width: 768px) {
  .advantages-item__background-blue__pic-town {
    background-image: none;
  }
}
.advantages-item__background-blue__pic-town::after {
  content: "";
  display: block;
  max-width: 520px;
  height: 260px;
  background-color: #b4d1ca;
  background-image: url("../img/mobile/advantages-town.jpg");
  background-position: center 0;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .advantages-item__background-blue__pic-town::after {
    background-image: url("../img/mobile/advantages-town@2.jpg");
  }
}
@media (min-width: 520px) {
  .advantages-item__background-blue__pic-town::after {
    width: 520px;
    max-width: none;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .advantages-item__background-blue__pic-town::after {
    width: 512px;
    height: 256px;
    background-image: url("../img/tablet/advantages-town-512.jpg");
    grid-column-start: 2;
    grid-column-end: 4;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .advantages-item__background-blue__pic-town::after {
    background-image: url("../img/tablet/advantages-town-1024.jpg");
  }
}
@media (min-width: 1200px) {
  .advantages-item__background-blue__pic-town::after {
    width: 800px;
    background-image: url("../img/desktop/advantages-town-800.jpg");
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .advantages-item__background-blue__pic-town::after {
    background-image: url("../img/desktop/advantages-town-1600.jpg");
  }
}
.advantages-item__background-blue__pic-canyon::after {
  content: "";
  display: block;
  max-width: 520px;
  height: 260px;
  background-color: #c9a47a;
  background-image: url("../img/mobile/advantages-canyon.jpg");
  background-position: center 0;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .advantages-item__background-blue__pic-canyon::after {
    background-image: url("../img/mobile/advantages-canyon@2.jpg");
  }
}
@media (min-width: 520px) {
  .advantages-item__background-blue__pic-canyon::after {
    width: 520px;
    max-width: none;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .advantages-item__background-blue__pic-canyon::after {
    width: 512px;
    height: 256px;
    background-image: url("../img/tablet/advantages-canyon-512.jpg");
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 2;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .advantages-item__background-blue__pic-canyon::after {
    background-image: url("../img/tablet/advantages-canyon-1024.jpg");
  }
}
@media (min-width: 1200px) {
  .advantages-item__background-blue__pic-canyon::after {
    width: 800px;
    background-image: url("../img/desktop/advantages-canyon-800.jpg");
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .advantages-item__background-blue__pic-canyon::after {
    background-image: url("../img/desktop/advantages-canyon-1600.jpg");
  }
}
